@import url(https://fonts.googleapis.com/css?family=Baloo|Nunito);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.wrapper {
  font-family: Nunito;
  margin: 0;
  padding: 5vh;
  display: flex;
  justify-content: center;
  background: #0097a7;
}

.main {
  min-width: 90%;
  margin: 0 5%;
  padding: 2% 5%;
  background: #fff;
}

.site-title {
  font-family: Baloo;
}

.article-section {
  height: 60vh;
  overflow: scroll;
  background-color: rgba(0,0,0,.1);
  border-top: 2px solid #fff;
}

.article {
  /* box-shadow: 0px 13px 40px -13px rgba(0,0,0,0.75); */
  /* padding: 2%; */
  width: 96% !important;
  margin: 2rem 2% !important;
  background-color: #fff;
}

h3.article_key {
  margin-top: 0;
}

img {
  max-width: 100%;
  height: auto;
}
